  .slider {

    box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
    height: 100px;
    margin: auto;
    overflow: hidden;
    position: relative;
    width: 960px;
    margin-top: 100px;
    margin-bottom: 70px;
  }


  .slider::before, .slider::after {
    background: linear-gradient(to right, white 0%, rgba(0, 0, 0, 0) 100%);
    content: "";
    height: 100px;
    position: absolute;
    width: 200px;
    z-index: 2;
  }
  .slider::after {
    right: 0;
    top: 0;
    transform: rotateZ(180deg);
  }
  .slider::before {
    left: 0;
    top: 0;
  }
  .slider .slide-track {
    -webkit-animation: scroll 40s linear infinite;
            animation: scroll 40s linear infinite;
    display: flex;
    width: calc(250px * 14);
  }
  .slider .slide {
    height: 100px;
    width: 250px;
  }

  @media (max-width: 640px) {
    .slider {

    
      height: 100px;
      margin: auto;
      overflow: hidden;
      position: relative;
      width: 350px;
      margin-top: 90px;
      margin-bottom: 70px;
    }

    .slider::before, .slider::after {

      content: "";
      height: 100px;
      position: absolute;
      width: 200px;
      z-index: 2;
    }
  }





